import { BaseCommand } from './BaseCommand';
import { Command, SetCardCommandData } from '../types/commands';
import { MoveCardCommand } from './MoveCardCommand';

export class SetCardCommand extends BaseCommand {
    private data: SetCardCommandData;
    private moveCardCommand: Command;

    constructor(data: SetCardCommandData) {
        super();
        this.type = "Set ST";
        this.data = data;

        this.moveCardCommand = new MoveCardCommand({
            player: this.data.player,
            type: this.type,
            id: this.data.id,
            originZone: this.data.originZone,
            zone: this.data.zone,
            position: "facedown"
        });
    }

    exec(): void {
        this.execChildCommand(this.moveCardCommand);
    }

    undo(): void {
        this.undoChildCommand(this.moveCardCommand);
    }
}